<template>
  <div class="main">
    <div class="crumb_box">
      <div class="search_box">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item>首页</el-breadcrumb-item>
          <el-breadcrumb-item>设备管理</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    </div>
    <div class="clear"></div>
    <div class="application_item">
      <div class="table_item">
        <el-table ref="multipleTable" stripe :data="deviceLists" tooltip-effect="dark" height="calc(100vh - 205px)" style="width: 100%;border-bottom: none">
          <el-table-column prop="deviceName" label="设备类型" width="150" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
          <el-table-column prop="version" label="设备版本" width="100" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
          <el-table-column prop="loginDate" label="最后登录时间" :formatter="dateFormat" width="130" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
          <el-table-column> </el-table-column>
          <el-table-column label="操作" width="190">
            <template slot-scope="scope" class="btn_txt" >
              <el-button type="text" @click="deleteDevices(scope.row.deviceId)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!--<div class="pagination">-->
        <!--<el-pagination-->
          <!--v-if='paginations.total > 0'-->
          <!--:page-sizes="paginations.page_sizes"-->
          <!--:page-size="paginations.page_size"-->
          <!--:layout="paginations.layout"-->
          <!--:total="paginations.total"-->
          <!--:current-page.sync='paginations.page_index'-->
          <!--@current-change='handleCurrentChange'-->
          <!--@size-change='handleSizeChange'>-->
        <!--</el-pagination>-->
      <!--</div>-->
    </div>

  </div>
</template>

<script>
  import {deleteDevice,getDeviceList} from '@/api/UserApi';
  import moment from 'moment'
  export default {
    name: "DeviceManagement",
    data() {
      return {
        tableConfig: {
          loading: false,
          overflow: true,
        },
        radio:0,
        totalTableData: [],
        deviceLists:[
          // {
          //   deviceId: 331,//设备id
          //   deviceName: "HUAWEI FRD-AL00",//设备名称
          //   version: "23",//设备版本号
          //   loginDate: 1535090299000//最后登录时间
          // },
        ]
      }
    },
    created(){
      this.getDeviceLists();
    },
    methods:{
      dateFormat: (row, column)=> {
        const date = row[column.property]
        if (date === undefined) {
          return ''
        }
        //这里的格式根据需求修改
        return moment(date).format('YYYY-MM-DD')
      },
      dateFrm: (e) =>{
        // console.log(e);
        return moment(e).format('YYYY-MM-DD');
      },
      //获取设备信息列表
      getDeviceLists: function () {
        getDeviceList().then((res) => {
          if (res.data.code === this.$global.successCode) {
            //console.log("请求设备列表信息数据成功",res);
            this.deviceLists = res.data.data;
          }
        }, function (error) {
          console.log("请求设备列表信息数据失败",error);
        });
      },
      //删除设备
      deleteDevices(corId){
        this.$confirm('是否删除该设备？', '提示', {confirmButtonText: '确定', cancelButtonText: '取消', type: 'warning'}).then(() => {
          deleteDevice({deviceId: corId}).then((res) => {
            if (res.data.code === this.$global.successCode) {
              //console.log("删除设备成功",res);
              this.$message({
                message: "删除设备成功！",
                type: "success"
              });
              this.getDeviceLists();
            }
          }, function (error) {
            console.log("删除设备失败",error);
          });
        }).catch(() => {
        });


      },

    }
  }
</script>

<style scoped>

</style>





